.container {
    background-color: rgb(227, 242, 253);
    min-height: 90vh;
    height: fit-content;
    padding: 30px;
    display: flex;
    justify-content: center;
}

/* .date-picker {
    z-index: index;
} */

.head-n-date {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
}

.content {
    width: 80%;
    height: 80%;
    background-color: white;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 30px;
    gap: 30px;
}

